@import url('https://fonts.googleapis.com/css2?family=Honk&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  margin: 0;
  background: #151414;
  color: #fff;
  font-family: 'Montserrat', sans-serif;

}

header {

}

.App {
  display: flex;
  width: 100%;
    height: 100vh;
  align-items: center;
}

.App__Right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App__Right img {
  width: 100%;
  max-width: 500px;
}

.App__Left {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.App__Left__Buttons {
    display: flex;
    gap: 16px;
    margin-top: 32px;
}

.App h1 span {
  font-family: 'Honk', system-ui;
  font-size: 80px;
}
.App h1 {
    font-size: 70px;
    margin: 0;
    padding: 0;
    font-weight: 900;
  margin-bottom: -12px;
}

.App button {
  background-color: #121214;
  color: #FFFFFF;
  box-shadow: 0 4px 24px rgba(0,0,0, 0.16);
  padding: 8px 16px 8px 12px;
  display: flex;
  align-items: center;
  gap: 4px;
  height: 40px;
  border: none;
  border-radius: 100vh;
  cursor: pointer;
  font-size: 14px;
  font-weight: 590;
  line-height: 18px;
  transition: transform 0.125s ease-in-out;
  outline: none;
  font-style: normal;
  font-weight: 590;
  font-size: 15px;
  line-height: 18px;
  color: #FFFFFF;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Helvetica Neue', Arial, Tahoma, Verdana, sans-serif;
}

@media (min-width: 320px) and (max-width: 480px) {

  .App {
    flex-direction: column;
    padding: 48px;
    width: fit-content;
  }

  p {
    max-width: calc(100vw - 48px - 48px);
  }

  .App__Right img {
    max-width: calc(100vw - 48px - 48px);
  }

  .App h1 span {
    font-size: 50px;
  }

    .App h1 {
        font-size: 40px;
      text-align: center;
    }

}